    margin-bottom: 30px;
}

#text-2 {
    top: 96px;
}

.animated-text, .animated-text2 {
    white-space: nowrap;
    font-family: 'Audrey', Tahoma, sans-serif;
    font-size: 64px;
}

.animated-text {
    animation: Scroll 8s linear infinite;
}

.animated-text2 {
    animation: Scroll2 8s linear infinite;
}

@keyframes Scroll {
    0% {
        transform: translateX(-100%);
    }
    100% {
        transform: translateX(0%);
    }
}

@keyframes Scroll2 {
    0% {
        transform: translateX(0%);
    }
    100% {
        transform: translateX(-100%);
    }
}

// section services

.services, .services-list {
    background-color: #F9F1E8;
    height: fit-content;
}

.services-container {
    display: grid;
    grid-template-columns: repeat(2,minmax(0,1fr));
    grid-column-gap: 85px;
    grid-row-gap: 100px;
    grid-auto-rows: min-content;
}

.services-container .services-component:nth-child(2n), .works:nth-child(2n), .post:nth-child(2n) {
    transform: translatey(20%);
}

.services-component, .services-list-component, .portfolio-component, .blog-component {
    display: grid;
    border: 2px solid #000;
    border-radius: 50px;
}

.services-component, .services-list-component {
    grid-template-columns: 1fr 1fr;
}

.portfolio-component, .blog-component {
    grid-template-columns: 70% 30%;
    height: 100%;
}

.services-list-component {
    margin-bottom: 5%;
}

.services-component-content, .services-list-component-content {
    grid-column-start: 1;
    grid-column-end: 3;
    display: grid;
    padding: 20px;
    border-bottom: 2px solid;
    column-gap: 5%;
 }

.services-component-content {
    grid-template-columns: 55% 40%;
    grid-template-rows: 30%;
}

.services-list-component-content {
    grid-template-columns: repeat(3, 1fr);
}

#audit .services-list-component-content {
    grid-template-columns: none;
}

.services-component-content > h3 {
    grid-column-start: 1;
    grid-row-start: 1;
    font-size: 40px;
}

.services-component-content > img, .services-list-component-content > img {
    grid-row-start: 1;
    grid-row-end: 3;
    border-radius: 50px;
    margin: auto;
}

.services-list-component-content > img {
    grid-row-start: 1;
    grid-row-end: 3;
    border-radius: 50px;
    margin: auto;
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.services-component-content > p {
    padding-top: 20px;
}

.services-list-component-content > h2 {
